import { Injectable } from '@angular/core';
import { Subject, Observable, throwError } from 'rxjs';
import { IProduct } from './product.model';
import { tap, catchError } from 'rxjs/operators';
import {
  HttpClient,
  HttpErrorResponse,
  HttpHeaders,
} from '@angular/common/http';

@Injectable()
export class ProductService {
  private domain: string = 'https://jtlfashion.herokuapp.com';
  // private domain: string = "http://127.0.0.1:4000";
  private loginUrl = `${this.domain}/api/v1/user/login`;
  private createProdUrl = `${this.domain}/api/v1/product`;

  constructor(private http: HttpClient) { }

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occured: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
  }

  createProduct(formData) {
    let options = {
      headers: new HttpHeaders({ 'Content-Type': 'multipart/form-data' }),
      withCredentials: true,
    };

    let prod = this.http
      .post<any>(this.createProdUrl, formData, { withCredentials: true })
      .pipe(
        tap((data) => {
          console.log(data);
        })
      )
      .pipe(catchError(this.handleError));

    prod.subscribe(() => {
      console.log('You In');
    });
  }

  getProducts(): Observable<IProduct[]> {
    let subject = new Subject<IProduct[]>();
    setTimeout(() => {
      subject.next(products);
      subject.complete();
    }, 200);
    return subject;
  }

  getProduct(id): IProduct {
    return products.find((el) => el._id === id);
  }
}

