import { Component, ViewChild, ElementRef, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthService } from './auth.service';

@Component({
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
  profileForm: FormGroup;
  private name: FormControl;
  private gender: FormControl;
  private phone: FormControl;

  constructor(private router: Router, private authService: AuthService) {}

  ngOnInit() {
    this.name = new FormControl(this.authService.currentUser.name, [
      Validators.required,
      Validators.pattern('[a-zA-Z].*'),
    ]);
    this.gender = new FormControl(this.authService.currentUser.gender);
    this.phone = new FormControl(this.authService.currentUser.tel);

    this.profileForm = new FormGroup({
      name: this.name,
      gender: this.gender,
      phone: this.phone,
    });
  }
  updateProfile(formValue) {
    if (this.profileForm.valid) {
      this.authService.updateCurrentUser(formValue);
      this.router.navigate(['/products']);
    }
  }

  validateName() {
    return this.name.valid || this.name.untouched;
  }

  validatePhone() {
    return this.phone.valid || this.phone.untouched;
  }

  cancel() {
    this.router.navigate(['/products']);
  }
}

