import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { UserModule } from './user/user.module';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';

import {
  ProductService,
  ProductsListResolver,
  ProductRouteActivator,
  ProductListComponent,
  ProductThumbnailGrid,
  ProductDetailsComponent,
  CreateProductComponent,
} from './products/index';

import { CategoryService } from './categories/shared/category.service';
import { ZolaAppComponent } from './zola-app.component';
import { HeaderComponent } from './nav/header.component';
import { UserNavbarComponent } from './nav/user-navbar.component';
import { Error404Component } from './errors/404.component';

import { ToastrService } from './common/toastr.service';
import { appRoutes } from './routes';
import { AuthService } from './user/auth.service';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    UserModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes),
  ],
  declarations: [
    ZolaAppComponent,
    HeaderComponent,
    UserNavbarComponent,
    ProductListComponent,
    ProductThumbnailGrid,
    ProductDetailsComponent,
    CreateProductComponent,
    Error404Component,
  ],
  providers: [
    AuthService,
    ProductService,
    ToastrService,
    ProductRouteActivator,
    ProductsListResolver,
    CategoryService,
    { provide: 'canDeactivateCreateProduct', useValue: checkDirtyState },
  ],
  bootstrap: [ZolaAppComponent],
})
export class AppModule {}

export function checkDirtyState(component: CreateProductComponent) {
  if (component.isDirty) {
    return window.confirm(
      'You have not saved this product, do you really want to cancel?'
    );
  }
  return true;
}
